@import "helper";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

*::before, *::after {
  box-sizing: border-box;
}

html {
  min-height: -webkit-fill-available;
}

body {
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
  color: #333;
  background: #202124;
  @media (min-width: 465px) {
    width: 465px;
    margin-left: auto;
    margin-right: auto;
  }
}

#root {

}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

:focus {
  outline: none;
}

.icon {
  height: 1em;
  width: 1em;
  display: inline-block;
}

button, input {
  font: inherit;
  color: #333;
}

input[type="date"] {
  font-size: 12px;
  width: 50px;
  padding: 0;
}

.clearfix::after {
  content: '';
  clear: both;
  display: block;
}

.oneLine {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.scroll {
  overflow: auto;
  -ms-overflow-style: none; /* IE and Edge */
}

.scroll::-webkit-scrollbar {
  display: none;
}
